import React, { useContext } from "react";
import { Link } from "react-router-dom";
import { DataContacts } from '../../../App'; 
import './style.css';
import './media.css';

function NavFooter() {

    const { setHomeActive,  setAboutActive,setNursaryActive,  setSchoolActive, setPricesActive, setContactsActive, setScrollY  } = useContext(DataContacts);

    function removeHover() {     

        let elemLi = document.querySelector('.hover');
        if(!elemLi) return;
        elemLi.className = '';
    }

    function hover(event){
        let elem = event.target;
        let elemLi = elem.closest('li');
        if(!elemLi) return;
        if (elemLi.className === 'hover') {
            elemLi.className = '';
        } else {

            const linkLi = document.querySelectorAll('.hover');
            if (!linkLi) return;
            
            linkLi.forEach(elem => {
                elem.classList.remove('hover');
            });
            elemLi.className = 'hover';
        }
    }

    function pageTop(){
        let mainElem = document.querySelector('.main__content');
        mainElem.scrollIntoView({ block: "start"});
        setScrollY(0);
    }
    
    function hoverOut(){
        const linkLi = document.querySelectorAll('.hover');
        if (!linkLi) return;
        
        linkLi.forEach(elem => {
            elem.classList.remove('hover');
        });
    }

	return (
		<nav className="footer__nav" >
            <ul>
            <li ><Link id="home"  className="footer__nav__link nursary_primary_school" onClick={()=>{ setHomeActive(true); setAboutActive(false); setNursaryActive(false); setSchoolActive(false); setPricesActive(false); setContactsActive(false); setPricesActive(false);pageTop();}} onMouseOver={hover} to="/nursary_primary_school/"></Link></li>
                <li>
                    <Link className="footer__nav__link about" onClick={hover} > </Link>
                    <ul onMouseLeave={hoverOut}>
                        <li><Link onClick={()=>{setAboutActive(true); setHomeActive(false);  setNursaryActive(false);setSchoolActive(false);  setPricesActive(false); setContactsActive(false);   removeHover(); pageTop();}} to="/about/about/">O </Link></li>
                        <li><Link onClick={()=>{setAboutActive(true); setHomeActive(false);  setNursaryActive(false);setSchoolActive(false);  setPricesActive(false); setContactsActive(false);   removeHover(); pageTop();}} to="/about/staff/"> </Link></li>
                        <li><Link onClick={()=>{setAboutActive(true); setHomeActive(false);  setNursaryActive(false);setSchoolActive(false);  setPricesActive(false); setContactsActive(false);   removeHover(); pageTop();}} to="/about/events/"> </Link></li>
                    </ul>
                </li>
                <li >
                    <Link className="footer__nav__link nursary" onClick={hover} ></Link>
                    <ul onMouseLeave={hoverOut}>
                        <li><Link onClick={()=>{setNursaryActive(true); setHomeActive(false); setAboutActive(false); setSchoolActive(false); setPricesActive(false); setContactsActive(false);  removeHover(); pageTop();}} to="/nursary/junior/">  (3-4 )</Link></li>
                        <li><Link onClick={()=>{setNursaryActive(true); setHomeActive(false); setAboutActive(false); setSchoolActive(false); setPricesActive(false); setContactsActive(false);  removeHover(); pageTop();}} to="/nursary/middle/">  (4-5 )</Link></li>
                        <li><Link onClick={()=>{setNursaryActive(true); setHomeActive(false); setAboutActive(false); setSchoolActive(false); setPricesActive(false); setContactsActive(false);  removeHover(); pageTop();}} to="/nursary/senior/">  (5-6 )</Link></li>
                    </ul>
                </li>
                <li >
                    <Link className="footer__nav__link school" onClick={hover} ></Link>
                    <ul onMouseLeave={hoverOut}>
                        <li><Link onClick={()=>{setSchoolActive(true); setHomeActive(false); setAboutActive(false); setNursaryActive(false); setPricesActive(false); setContactsActive(false); removeHover(); pageTop();}} to="/school/preparatory/">   (6-7 )</Link></li>
                        <li><Link onClick={()=>{setSchoolActive(true); setHomeActive(false); setAboutActive(false); setNursaryActive(false); setPricesActive(false); setContactsActive(false); removeHover(); pageTop();}} to="/school/primary/">  (1-4 )</Link></li>
                    </ul>
                </li>
                <li><Link className="footer__nav__link prices"  onClick={()=>{setPricesActive(true); setHomeActive(false); setAboutActive(false); setNursaryActive(false); setSchoolActive(false); setContactsActive(false); pageTop();}} onMouseOver={hover} to="/prices/"></Link></li>
                <li><Link className="footer__nav__link contacts"  onClick={()=>{ setContactsActive(true); setHomeActive(false); setAboutActive(false); setNursaryActive(false); setSchoolActive(false); setPricesActive(false); pageTop();}} onMouseOver={hover} to="/contacts/"></Link></li>
            </ul>
        </nav>
	);
}

export default NavFooter;