import React, { useContext, useState, useEffect,  useRef } from "react";
import { Link } from "react-router-dom";

import { MainContext } from '../Main';
import { DataContacts } from '../../App';

import Girl from '../../images/Girl.png';
import Boy from '../../images/Boy.png';
import GirlBoyBalls from '../../images/GirlBoyBalls.png';
import GoogleMap from '../../images/map_preloder.png'; 

import { Swiper, SwiperSlide } from "swiper/react";
import { Pagination, Navigation, Mousewheel } from "swiper";
import 'swiper/swiper-bundle.min.css';
import 'swiper/swiper.min.css';
import 'swiper/modules/pagination/pagination.min.css';

import HeaderFormContacts from '../elements/headerContactsForm/HeaderFormContacts';


function Home() {
	const { setCreateContent, setNameContent, setContentAbout, ReviewContent } = useContext(MainContext);
	const { setHomeActive,  setAboutActive, setNursaryActive,  setSchoolActive, setPricesActive,  setContactsActive, screenWidth, setScrollY  } = useContext(DataContacts);

	const [isLoading, setLoading] = useState(true);
	const [btn, setBtn] = useState(false);
	const [indxReview, setIndxReview] = useState(0);
	const [button, setButton] = useState(false);

	const reviewFull = useRef();
	const refSlider = useRef();

	function pageTop(){
        let mainElem = document.querySelector('.main__content');
        mainElem.scrollIntoView({ block: "start"});
		setScrollY(0);
    }

	function handleOnLoad() {
		setLoading(false);
	}

	function Preloader(){
		return (
			<img className='map__preloader' src={GoogleMap} alt="Google map" />
		)	
	}

	function removeBtnReview() {
		let reviewFullElem = reviewFull.current;

		reviewFullElem.style.animation = 'timeout 1s ease-in-out 1 forwards';
		setTimeout(()=>{setBtn(false);setIndxReview(0)}, 1000);
    }

	function setFuncBtn(){
		setBtn(true);
		return;
	}

	function getFuncIndxReview(data){
		setIndxReview(data);
		return;
	}

	useEffect(()=> {
		getItemReview();
	},[])
	
	function getItemReview() {
		let dataFullContentReview = ReviewContent[indxReview];
		if(!dataFullContentReview) return;
			return (
			<div className="review__full-content_wrapper">
				<div ref={reviewFull} className="review__item__full">
					<button onClick={()=>{removeBtnReview();}}className="review__btn_remove"></button>
					<div>
						<div className="review__item__img">
								<img src={dataFullContentReview.src}  alt={dataFullContentReview.alt}></img>
						</div>
						<div className="review__item__title"><span>{dataFullContentReview.name}</span><span>{dataFullContentReview.date}</span></div>
					</div>
					<div>
						<div className="review__full-content">{dataFullContentReview.fullReview}</div>
					</div>
				</div>
			</div>	
		)
	}

	function showHeaderForm() {
        setButton(true);
        return;
    }
	
	return (
		<div className="page__home">
			<div className="home__present">
				<div className="home__present__content">
					<h1 className="home__present__content_title">     --</h1>
					<p className="home__present__content_description">    30 !   ,          .</p>
					<Link onClick={()=>{setHomeActive(false); setAboutActive(true); setNursaryActive(false); setSchoolActive(false); setPricesActive(false); setContactsActive(false); setPricesActive(false); pageTop();}} className="link__inDetails" to="/about/about/"><span></span><span></span><span className="link__inDetails_arrow"></span></Link>
					{screenWidth < 426 && <button className="header__btn" onClick={ showHeaderForm }> </button>}
                        {button === true && <HeaderFormContacts button={{ setButton}}/>
                        }
				</div>
				<div className="home__present__content_photos">
					<div className="home__present__content_curcle1"></div>
					<div className="home__present__content_curcle2"></div>
					<div className="home__present__content_curcle3"></div>
					<div className="home__present__content_photos1"><img src={Girl} alt=""></img></div>
					<div className="home__present__content_photos2"><img src={Boy} alt=""></img></div>
					<div className="home__present__content_photos3"><img src={GirlBoyBalls} alt="  "></img></div>
				</div>
			</div>
			<div className="home__advantages">
				<h3 className="home__advantages_title">    </h3>
				<ul>
					<li className="advantages__items"><span className="advantages__sign_item1"></span><span>    </span></li>
					<li className="advantages__items"><span className="advantages__sign_item2"></span><span>   </span></li>
					<li className="advantages__items"><span className="advantages__sign_item3"></span><span>   </span></li>
					<li className="advantages__items"><span className="advantages__sign_item4"></span><span>    </span></li>
				</ul>
				<ul>
					<li className="advantages__items"><span className="advantages__sign_item5"></span><span>  </span></li>
					<li className="advantages__items"><span className="advantages__sign_item6"></span><span>  </span></li>
					<li className="advantages__items"><span className="advantages__sign_item7"></span><span>   </span></li>
					<li className="advantages__items"><span className="advantages__sign_item8"></span><span> </span></li>
				</ul>
			</div>
			<div className="home__nursary">
				<div className="home__nursary__content">
					<h4 className="home__nursary__content_title"> </h4>
					<p className="home__nursary__content_description">  -                   .<br/><br/>
					         :  ,  ,  ,     .<br/> <br/>
					       .          .</p>
				</div>
				<div className="home__nursary__photo">
					<div className="home__nursary__babies_photo"><ul>    :
						<li> (2-3 )</li>
						<li>C (4-5 )</li>
						<li>C (5-6 )</li>
					</ul></div>
					<div className="home__nursary__link" >
						<span className="home__nursary__link_price"> 330 $  </span><Link onClick={() => {setHomeActive(false); setAboutActive(false); setNursaryActive(true); setSchoolActive(false); setPricesActive(false); setContactsActive(false); setPricesActive(false); pageTop();}} className="link__inDetails" to="/nursary/junior/"><span></span><span></span><span className="link__inDetails_arrow"></span></Link>
					</div>
				</div>
			</div>
			<div className="home__preparatory">
				<div className="home__preparatory__photo">
					<div className="home__preparatory__kids_photo"><span>          --,       .</span></div>
					<div className="home__preparatory__link" >
						<span className="home__preparatory__link_price"> 372 $  </span><Link onClick={() => {setHomeActive(false); setAboutActive(false); setNursaryActive(false); setSchoolActive(true); setPricesActive(false); setContactsActive(false); setPricesActive(false); pageTop();}} className="link__inDetails" to="/school/preparatory/"><span></span><span></span><span className="link__inDetails_arrow"></span></Link>
					</div>
				</div>
				<div className="home__preparatory__content">
					<h4 className="home__preparatory__content_title"> </h4>
					<p className="home__preparatory__content_description">     :</p>
					<ul>
						<li>  +  , .</li>
						<li>  + , .</li>
						<li> .</li>
						<li>  .</li>
						<li>.</li>
						<li>.</li>
						<li> .</li>
					</ul>
				</div>
			</div>
			<div className="home__primary">
				<div className="home__primary__content">
					<h4 className="home__primary__content_title">  </h4>
					<p className="home__primary__content_description">
					   --,   1  4        8.00  15.00   8.00  17.00.<br/> <br/> 
					   ,        .<br/> <br/>
					        1  (     (YLE)).<br/> <br/>
					        .</p>
				</div>
				<div className="home__primary__photo">
					<div className="home__primary__kids_photo"><span>  1  4 ,      </span></div>
					<div className="home__primary__link" >
						<span className="home__primary__link_price"> 470 $  </span><Link onClick={() => {setHomeActive(false); setAboutActive(false); setNursaryActive(false); setSchoolActive(true); setPricesActive(false); setContactsActive(false); setPricesActive(false); pageTop();}} className="link__inDetails" to="/school/primary/"><span></span><span></span><span className="link__inDetails_arrow"></span></Link>
					</div>
				</div>
			</div>
			<div className="video_content">
				<div className="video_content_title">  3D-  -- </div>
				<button className="video_content_btn" onClick={() => { setNameContent('video'); setContentAbout('school_tour'); setCreateContent(true); }} ><span></span><span></span></button>
			</div>

			<div className="home__review">
				<h4 className="home__review__content_title"></h4>
				{btn && <>{getItemReview()}</> }
				<div  className="review__content_slider">
					<div ref={refSlider} className="review__content_wrapper">
					<Swiper
        				slidesPerView={screenWidth > 425 ? 2 : 1}
        				spaceBetween={10}
        				loop={true}
        				loopFillGroupWithBlank={true}
        				pagination={{
        				//   clickable: true,
						  type: "fraction",
        				}}
        				navigation={true}
        				modules={[Pagination, Navigation, Mousewheel]}
						mousewheel={true}
						keyboard={true}
						longSwipesMs={1000}
        				className="mySwiper"
      >
					{ <>{ReviewContent.map((item, index) => {
            	    		    return (
            	    		        <SwiperSlide key={index}><div   className={`review__item item${index+1}`} >
										<div>
											<div className="review__item__img">
												<img src={item.src}  alt={item.alt}></img>
											</div>
											<div className="review__item__title"><span>{item.name}</span><span>{item.date}</span></div>
										</div>
										<div>
											<div className="review__short-content">{item.shortReview}</div>
											<button onClick={()=>{setFuncBtn(); getFuncIndxReview(index); }} className="review__btn"> </button>
										</div>
									</div>
									</SwiperSlide>
            	    		    )
            	    		})}</>
						}
					</Swiper>
					</div>
            	</div>
			</div>
			<div className='page__contacts_iframe home_iframe'>
				{isLoading && <Preloader/>}
				{<iframe title='  ' src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2347.951570951808!2d27.587888115966827!3d53.950367980110066!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x46dbcf5cad0687f9%3A0x67db471bff3c1228!2z0YPQuy4g0J3QuNC60LjRgtC40L3QsCAzNSwg0JzQuNC90YHQug!5e0!3m2!1sru!2sby!4v1666895836881!5m2!1sru!2sby" onLoad={handleOnLoad}></iframe>}
				</div>
		</div>
	);
}

export default Home;